<template>
    <section class="hero is-bold app-navbar animated" :class="{ slideInDown: show, slideOutDown: !show }">
        <div class="hero-head">
            <nav class="nav">
                <div class="nav-left">
                    <a class="nav-item is-hidden-tablet" @click="toggleSidebar(!sidebar.opened)">
                        <i class="fa fa-bars" aria-hidden="true"></i>
                    </a>
                </div>
                <div class="nav-center">
                    <a class="nav-item hero-brand" href="/">
                        <img src="~assets/logo.png" :alt="pkginfo.description">

                    </a>
                </div>
                <div class="nav-right is-flex">
                    <a @click="logout" class="nav-item">Logout</a>
                </div>
            </nav>
        </div>
    </section>
</template>

<script>

    import {mapGetters, mapActions} from 'vuex'

    export default {

        components: {},

        props: {
            show: Boolean
        },

        computed: mapGetters({
            pkginfo: 'pkg',
            sidebar: 'sidebar'
        }),

        methods: {
            ...mapActions([
                'toggleSidebar'
            ]),
            logout () {
                this.$http.get('/admin/logout').then((response) => {
                    if (response.body.success) {
                        window.location.href = response.body.url
                    }
                }, (response) => {
                    console.log(response)
                })
            }
        }
    }
</script>

<style lang="scss">

    .app-navbar {
        position: fixed;
        min-width: 100%;
        z-index: 1024;
        box-shadow: 0 2px 3px rgba(17, 17, 17, 0.1), 0 0 0 1px rgba(17, 17, 17, 0.1);

        .container {
            margin: auto 10px;
        }

        .nav-right {
            align-items: stretch;
            align-items: stretch;
            flex: 1;
            justify-content: flex-end;
            overflow: hidden;
            overflow-x: auto;
            white-space: nowrap;
        }
    }

    .hero-brand {
        .vue {
            margin-left: 10px;
            color: #36AC70;
        }
        .admin {
            color: #28374B;
        }
    }
</style>



